.icon {
  font-family: "social"; }
  .icon:before {
    /* Some common icon stuff */ }
  .icon.icon--search:before {
    content: ""; }
  .icon.icon--telegram:before {
    content: ""; }

* {
  box-sizing: border-box; }

html, body {
  font-family: 'Roboto', sans-serif; }

html {
  font-size: 10px;
  color: #6b6b6b;
  background-color: #fafafa; }

body {
  font-size: 16px; }

a {
  display: inline-block;
  text-decoration: none;
  color: inherit; }

fieldset {
  border: none; }

ul {
  margin: 0;
  padding-left: 0;
  list-style-type: none; }

.search__input, .send__input {
  border-radius: 5px;
  border: 1px solid #ccc; }
  .search__input::placeholder, .send__input::placeholder {
    color: #ccc; }
  .search__input:focus, .send__input:focus {
    outline: none; }

.contact__image, .message__img {
  border-radius: 5px;
  overflow: hidden; }

.section {
  padding: 5rem 0; }

.container {
  max-width: 76.8rem;
  margin: 0 auto; }

.chat {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  background-color: #EBEBE9; }
  .chat__contacts {
    width: 25rem;
    padding: 1rem;
    z-index: 10; }
  .chat__window {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    width: calc(100% - 25rem);
    background-color: #fff; }
  .chat__messages {
    height: 40rem;
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
    padding: 2rem;
    overflow-y: scroll; }
    .chat__messages::-webkit-scrollbar {
      width: 5px; }
    .chat__messages::-webkit-scrollbar-thumb {
      border-radius: 2px;
      box-shadow: inset 10px 0 0 #5DAEDD; }

.search {
  display: flex;
  position: relative;
  margin-bottom: 2rem;
  font-size: 1.4rem; }
  .search__input {
    width: 100%;
    padding: .5rem 1rem; }
    .search__input:focus {
      border-color: #000; }
      .search__input:focus + .search__icon, .search__input:focus::placeholder {
        color: #000; }
  .search__icon {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 1rem;
    color: #ccc; }

.contact {
  display: flex;
  align-items: center;
  margin-top: .5rem;
  padding: 1rem;
  cursor: pointer; }
  .contact__image {
    width: 5rem;
    height: 5rem;
    margin-right: 1rem; }
  .contact__name {
    width: calc(100% - 60px);
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden; }
  .contact.is-active {
    background-color: #5DAEDD;
    color: #fff; }

.send {
  display: flex;
  align-items: center;
  border-top: 1px solid #ccc;
  padding: 2rem;
  font-size: 1.4rem; }
  .send__input {
    width: calc(100% - 5rem);
    margin-right: 2rem;
    padding: .7rem 1rem; }
    .send__input:focus {
      border-color: #5DAEDD;
      color: #5DAEDD; }
      .send__input:focus::placeholder {
        color: #5DAEDD; }
      .send__input:focus + .send__btn {
        color: #5DAEDD; }
  .send__btn {
    width: 3rem;
    height: 3rem;
    padding: 0;
    position: relative;
    background-color: transparent;
    border: none;
    font-size: 3rem; }
    .send__btn .icon {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%); }

.message {
  position: relative;
  display: flex;
  align-items: flex-start;
  width: 100%;
  margin: 2rem 0;
  word-wrap: break-word; }
  .message__img {
    width: 7rem;
    height: 7rem; }
  .message__text {
    position: relative;
    width: 56%;
    min-height: 4rem;
    margin: 0;
    padding: 1rem;
    color: #fff;
    border-radius: 5px;
    font-size: 1.4rem; }
    .message__text:after {
      content: "";
      position: absolute;
      top: 50%; }
  .message--sended .message__img {
    margin-right: 3rem; }
  .message--sended .message__text {
    background-color: #5DAEDD; }
    .message--sended .message__text:after {
      left: 0;
      transform: translate(-100%, -50%);
      width: 0;
      height: 0;
      content: '';
      z-index: 2;
      border-right: 1rem solid #5DAEDD;
      border-bottom: 1rem solid transparent;
      border-top: 1rem solid transparent; }
  .message--received {
    justify-content: flex-end; }
    .message--received .message__img {
      order: 1;
      margin-left: 3rem; }
    .message--received .message__text {
      background-color: #2DCC70; }
      .message--received .message__text:after {
        right: 0;
        transform: translate(100%, -50%);
        width: 0;
        height: 0;
        content: '';
        z-index: 2;
        border-left: 1rem solid #2DCC70;
        border-bottom: 1rem solid transparent;
        border-top: 1rem solid transparent; }

.btn-menu {
  display: none;
  position: absolute;
  top: 1rem;
  right: 1rem;
  width: 4.5rem;
  padding: 1rem .7rem;
  color: #fff;
  background-color: #5DAEDD;
  border: none;
  border-radius: .5rem;
  cursor: pointer;
  z-index: 11; }
  .btn-menu:focus {
    outline: none; }
  .btn-menu__span {
    position: relative;
    display: block;
    height: 3px;
    background-color: #fff;
    transition: all .3s; }
    .btn-menu__span--mid {
      margin: .5rem 0; }
  .btn-menu.is-active .btn-menu__span--top {
    transform: rotate(45deg);
    top: 8px; }
  .btn-menu.is-active .btn-menu__span--mid {
    opacity: 0; }
  .btn-menu.is-active .btn-menu__span--bot {
    transform: rotate(-45deg);
    top: -8px; }

@media screen and (max-width: 767px) {
  html, body {
    height: 100%; }
  .section {
    padding: 0; }
  .chat,
  .chat__contacts,
  .chat__window {
    min-height: 100%; }
  .chat__contacts {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding-right: 7rem;
    background-color: #fff;
    transform: translateX(-100%);
    transition: transform .3s; }
  .chat__window {
    width: 100%;
    padding: 6rem 0 0 0; }
  .btn-menu {
    display: block; }
    .btn-menu.is-active + .chat__contacts {
      transform: translateX(0); } }
